import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { PickListViewComponent } from './pick-list-view.component';
import { Routes, RouterModule } from '@angular/router';
import { PickListService } from 'src/app/service/pick-list.service';

const routes: Routes = [
  {
    path: ':id',
    component: PickListViewComponent
  }
]


@NgModule({
  declarations: [PickListViewComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ], providers: [PickListService]
})
export class PickListViewModule { }
