import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { PickListService } from 'src/app/service/pick-list.service';
import { Location } from '@angular/common';
import { DataService } from 'src/app/service/data.service';


@Component({
  selector: 'app-pick-list-view',
  templateUrl: './pick-list-view.component.html'
})
export class PickListViewComponent implements OnInit {

  PageTitle = "PickList View";
  viewId: string;
  objectArray: any;
  orderDetails: any = {};
  productList: Array<any> = [];
  currentCompany:any;
  date_format:string="M/d/yy";
  timezone:string
  time_format:string;

  constructor(
    private activatedRoute: ActivatedRoute,
    private pickListService: PickListService,
    private dataService:DataService,
    private location: Location
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }

  ngOnInit() {
    this.dataService.currentCompany.subscribe((response)=>{
      if(response){
      this.timezone=String(response.timezone.format);;
      this.time_format=response.time_format.label;
      this.date_format=response.date_format.label;
      this.currentCompany=response;
      }
      
    });
    this.getObject();
  }

  back() {
    this.location.back();
  }

  getObject() {
    this.pickListService.getPickListGeneratedViewById(this.viewId).subscribe((response) => {
      if (response.success) {
        this.orderDetails = response.data.order_detail;
        this.productList = response.data.product;
        // this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.orderDetails = {};
        this.productList = [];
      }
    }, (error) => {
      this.orderDetails = {};
      this.productList = [];

    });
  }

  print() {
    window.print();
  }

}
